import React from 'react'
import { Line, Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  LineElement
} from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, BarElement);

interface ChartData {
  labels: string[],
  datasets: object[]
}

interface FormattedDataset {
  label: string,
  data: number[]
}

const Charts = () => {
  const chartOptions = {
    scales: {
      y: {
        beginAtZero: true
      }
    }
  }

  const [systemCount, setSystemCount] = React.useState<number>(0);
  const [probability, setProbability] = React.useState<number>(0);
  const [attacksCount, setAttacksCount] = React.useState<number>(0);

  const [trajectoryData, setTrajectoryData] = React.useState<Partial<ChartData>>({});
  const [frequencyData, setFrequencyData] = React.useState<Partial<ChartData>>({});
  const [relFrequencyData, setRelFrequencyData] = React.useState<Partial<ChartData>>({});
  const [normalizedData, setNormalizedData] = React.useState<Partial<ChartData>>({});

  const [called, setCalled] = React.useState<boolean>(false);

  const getInput = React.useCallback(() => {
    const _systemCount = (document.getElementById('systemCount') as HTMLInputElement).value ?? 0;
    const _attacksCount = (document.getElementById('attacksCount') as HTMLInputElement).value ?? 0;
    const _probability = (document.getElementById('probability') as HTMLInputElement).value ?? 0;

    setSystemCount(parseInt(_systemCount));
    setAttacksCount(parseInt(_attacksCount));
    setProbability(parseFloat(_probability));

    setCalled(true);
  }, []);

  const generateTrajectoryData = React.useCallback(() => {
    const systems: { [key: string]: { values: number[], currentValue: number } } = {};
    for (let i = 1; i <= systemCount; i++)
      systems["System " + i] = { values: [], currentValue: 0 };

    for (let j = 0; j < attacksCount; j++) {
      Object.keys(systems).forEach((key) => {
        const random = Math.random();
        systems[key].currentValue += random < probability ? 1 : -1;
        systems[key].values.push(systems[key].currentValue);
      });
    }

    let labels: string[] = [];
    const datasets: object[] = [];

    Object.keys(systems).forEach(key => {
      labels = [];

      for (let i = 1; i <= attacksCount; i++)
        labels.push("Pen " + i);

      datasets.push({
        label: key,
        data: systems[key].values,
        fill: false,
        borderColor: getRandomColor(),
        borderWidth: 1
      });
    });

    const reworkedDataSet = datasets.map(dataset => {
      return {
        label: dataset['label'],
        data: dataset['data'],
        backgroundColor: dataset['borderColor']
      }
    })

    return {
      labels,
      datasets: reworkedDataSet
    };
  }, [attacksCount, probability, systemCount]);

  const generateCumulativeFrequencyData = React.useCallback((dataset?: FormattedDataset[]) => {
    // interface Data {
    //   label: string,
    //   success: number,
    //   fail: number
    // }

    // const retval: Data[] = [];

    // let totalSuccess = 0;
    // let totalFail = 0;

    // dataset?.forEach(dataset => {
    //   const system: Data = {
    //     label: dataset.label,
    //     success: 0,
    //     fail: 0
    //   };

    //   dataset.data.forEach(attack => {
    //     system[attack === 1 ? 'success' : 'fail']++;
    //   })

    //   totalSuccess += system.success;
    //   totalFail += system.fail;

    //   retval.push({
    //     ...system,
    //     success: totalSuccess,
    //     fail: totalFail
    //   });
    // });

    // console.log({ retval })

    // return retval;

    const systems: {
      [key: string]: {
        values: number[],
        currentValue: number
        cumulativeFrequencies: number[],
        cumulativeFrequency: number
      }
    } = {};

    for (let i = 1; i <= systemCount; i++)
      systems["System " + i] = { values: [], cumulativeFrequencies: [], currentValue: 0, cumulativeFrequency: 0 };

    for (let j = 0; j < attacksCount; j++) {
      for (let system in systems) {
        const random = Math.random();

        systems[system].currentValue += random < probability ? 1 : -1;

        systems[system].values.push(systems[system].currentValue);
        systems[system].cumulativeFrequency += systems[system].currentValue;
        systems[system].cumulativeFrequencies.push(systems[system].cumulativeFrequency);
      }
    }

    let labels: string[] = [];
    const datasets: any[] = [];
    for (let system in systems) {
      labels = [];
      for (let i = 1; i <= attacksCount; i++) {
        labels.push("Pen " + i);
      }
      datasets.push({
        label: system,
        data: systems[system].cumulativeFrequencies,
        fill: false,
        borderColor: getRandomColor(),
        borderWidth: 1
      });
    }

    const reworkedDataSet = datasets.map(dataset => {
      return {
        data: dataset['data'],
        backgroundColor: dataset['borderColor']
      }
    })

    return {
      labels,
      datasets: reworkedDataSet
    };
  }, [attacksCount, probability, systemCount]);

  const generateRelFrequencyData = React.useCallback(() => {
    const systems: {
      [key: string]: {
        values: number[],
        currentValue: number
        relativeFrequencies: number[],
        cumulativeFrequency: number
      }
    } = {};

    for (let i = 1; i <= systemCount; i++) {
      systems["System " + i] = { values: [], relativeFrequencies: [], currentValue: 0, cumulativeFrequency: 0 };
    }

    for (var j = 0; j < attacksCount; j++) {
      for (let system in systems) {
        const random = Math.random();

        systems[system].currentValue += random < probability ? 1 : -1;

        systems[system].cumulativeFrequency += systems[system].currentValue;
        systems[system].values.push(systems[system].cumulativeFrequency);
        systems[system].relativeFrequencies.push(systems[system].cumulativeFrequency / (j + 1));
      }
    }

    let labels: string[] = [];
    const datasets: any[] = [];
    for (let system in systems) {
      labels = [];
      for (let i = 1; i <= attacksCount; i++) {
        labels.push("Pen " + i);
      }
      datasets.push({
        label: system,
        data: systems[system].relativeFrequencies,
        fill: false,
        borderColor: getRandomColor(),
        borderWidth: 1
      });
    }

    const reworkedDataSet = datasets.map(dataset => {
      return {
        data: dataset['data'],
        backgroundColor: dataset['borderColor']
      }
    })

    return {
      labels,
      datasets: reworkedDataSet
    };
  }, [attacksCount, probability, systemCount]);

  const generateNormalizedData = React.useCallback(() => {
    const systems: {
      [key: string]: {
        values: number[],
        currentValue: number
        normalizedRatios: number[],
        cumulativeFrequency: number
      }
    } = {};

    for (let i = 1; i <= systemCount; i++)
      systems["System " + i] = { values: [], normalizedRatios: [], currentValue: 0, cumulativeFrequency: 0 };

    for (let j = 0; j < attacksCount; j++) {
      for (let system in systems) {
        const random = Math.random();

        systems[system].currentValue += random < probability ? 1 : -1;

        systems[system].cumulativeFrequency += systems[system].currentValue;
        systems[system].values.push(systems[system].cumulativeFrequency);
        systems[system].normalizedRatios.push(systems[system].cumulativeFrequency / Math.sqrt(j + 1));
      }
    }

    let labels: string[] = [];
    const datasets: any[] = [];
    for (let system in systems) {
      labels = [];
      for (var i = 1; i <= attacksCount; i++) {
        labels.push("Pen " + i);
      }
      datasets.push({
        label: system,
        data: systems[system].normalizedRatios,
        fill: false,
        borderColor: getRandomColor(),
        borderWidth: 1
      });
    }

    const reworkedDataSet = datasets.map(dataset => {
      return {
        data: dataset['data'],
        backgroundColor: dataset['borderColor']
      }
    })

    return {
      labels,
      datasets: reworkedDataSet
    };
  }, [attacksCount, probability, systemCount]);

  const getRandomColor = () => {
    const colors = [
      '#FF0000',
      '#00FF00',
      '#0000FF',
      '#FFFF00',
      '#FF00FF',
      '#00FFFF',
      '#FF6600',
      '#6600FF',
      '#00FF66',
      '#FF0066',
    ]
    return colors[Math.floor(Math.random() * 10)];
  }

  React.useEffect(() => {
    if (called) {
      const dataset = generateTrajectoryData();

      const formattedDataset: FormattedDataset[] = dataset.datasets.map(elem => {
        return {
          label: elem.label,
          data: elem.data
        }
      });

      setTrajectoryData(dataset);
      // generateCumulativeFrequencyData(formattedDataset)
      setFrequencyData(generateCumulativeFrequencyData(formattedDataset));
      setRelFrequencyData(generateRelFrequencyData());
      setNormalizedData(generateNormalizedData());
      setCalled(false);
    }
  }, [trajectoryData, frequencyData, relFrequencyData, normalizedData, generateTrajectoryData, generateCumulativeFrequencyData, generateRelFrequencyData, generateNormalizedData, called]);

  return <>
    <table>
      <tbody>
        <tr>
          <td>
            <span>Systems Count:</span>
          </td>
          <td>
            <input className='border ml-2' type="number" id="systemCount" min="1" />
          </td>
        </tr>
        <tr>
          <td>
            <span>Attacks Count:</span>
          </td>
          <td>
            <input className='border ml-2' type="number" id="attacksCount" min="1" />
          </td>
        </tr>
        <tr>
          <td>
            <span>Penetration probability:</span>
          </td>
          <td>
            <input className='border ml-2' type="number" id="probability" step="0.01" min="0" max="1" />
          </td>
        </tr>
      </tbody>
    </table>
    <button
      id="generate"
      className='border-2 border-black rounded px-3 mt-5'
      onClick={getInput}
    >
      Generate
    </button>

    <div className='mt-10'>
      <h3>Trajectory</h3>
      <Line
        width={700}
        height={350}
        data={{
          labels: trajectoryData.labels,
          datasets: (trajectoryData.datasets as any[]) ?? []
        }}
        options={chartOptions}
      />
    </div>

    <div className='mt-10'>
      <h3>Cumulative Frequency</h3>
      <Line
        data={{
          labels: frequencyData.labels,
          datasets: (frequencyData.datasets as any[]) ?? []
        }}
        options={chartOptions}
      />
    </div>

    <div className='mt-10'>
      <h3>Relative Frequency</h3>
      <Line
        data={{
          labels: relFrequencyData.labels,
          datasets: (relFrequencyData.datasets as any[]) ?? []
        }}
        options={chartOptions}
      />
    </div>

    <div className='mt-10'>
      <h3>Normalized Frequency</h3>
      <Line
        data={{
          labels: normalizedData.labels,
          datasets: (normalizedData.datasets as any[]) ?? []
        }}
        options={chartOptions}
      />
    </div>
  </>
}

export default Charts;